<template>
  <div class="jl-information">
    <div class="jdy-content jdy-lv fleft jl-creditBill">
      <div class="jdy-content-inner">
        <el-form :model="ruleForm" :inline="true" :rules="rules" ref="ruleForm" class="mt10">
          <el-row class="search-row" style="width: 70%;">
            <el-form-item label="" prop="type">
              <el-select v-model="ruleForm.type" placeholder="" @change="changeType" class="select-m">
                <el-option v-for="item in type" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="type">
              <el-select v-model="ruleForm.type" placeholder="" @change="changeType" class="select-m">
                <el-option v-for="item in type" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="type">
              <el-select v-model="ruleForm.type" placeholder="" @change="changeType" class="select-m">
                <el-option v-for="item in type" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-date-picker v-model="ruleForm.dateRange" :picker-options="pickerOptions" type="daterange" @change="datetimerange" placeholder="" class="input-date-l">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="quickSearch" class="creditBillSearch">
              <el-input placeholder="////" @change="searchKeyWords" v-model="searchValue">
              </el-input>
            </el-form-item>
            <el-button type="primary" class="btnbg" @click="alertJournal"></el-button>
            <el-button type="default"  @click="mergeCreditBill"></el-button>
            <el-button type="default" @click="generateCreditBill"></el-button>
          </el-row>
        </el-form>
        <div class="jdy-table pt10">
          <el-table :data="tableData" border style="text-again: center" class="all" @row-click="rowclickHandler" highlight-current-row>
            <el-table-column type='selection' width="25" class-name="borderR0"></el-table-column>
            <el-table-column type='index' label=" " min-width="8" class-name="alignL"></el-table-column>
            <el-table-column label="" min-width="120">
              <template scope="scope">
                <!-- {{scope.row.nType}} -->
                2017-07-23
              </template>
            </el-table-column>
            <el-table-column label="" min-width="120">
              <template scope="scope">
                <!-- {{scope.row.nType}} -->
                2017-07-23
              </template>
            </el-table-column>
            <el-table-column label="" min-width="100">22</el-table-column>
            <el-table-column label="" min-width="60">2448.00</el-table-column>
            <el-table-column label="" min-width="320" class-name="txtHide">
              <template scope="scope">
                <!-- {{scope.row.nType}} -->
                ..
              </template>
            </el-table-column>
            <el-table-column label="" min-width="150" class-name="blue-font">
              <template scope="scope">
                <!-- {{scope.row.nType}} -->
                JDY20178801010933
              </template>
            </el-table-column>
            <el-table-column label="" min-width="150">
              <template scope="scope">
                <!-- {{scope.row.nType}} -->
                2017-06-22 11:07:48
              </template>
            </el-table-column>
          </el-table>
          <!--    begin-->
          <div class="clearfix">
            <el-pagination class="fright mt20" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pageSizeAll" layout="prev, pager, next, jumper" :total="tableDataTotal">
            </el-pagination>
          </div>
          <!--    end-->
        </div>
      </div>
    </div>
    <!--jdy-content end-->

    <!-- -  begin-->
    <jdy-alert title="" @closeAlert="closeAlert" v-if="alertMergeBillFlag" class="dialog-lv dialog-credit">
      <el-row class="scroll-cont p20">
        <el-form :model ="alertMergeBillSearchData" :inline="true">
          <el-row class="">
            <el-form-item label="">
              <el-date-picker type="daterange" placeholder="" v-model="value6" style="width: 210px;"></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="quickSearch">
              <el-input placeholder="" @change="searchKeyWords" style="width: 180px;" v-model="alertMergeBillSearchData.searchValue">
              </el-input>
            </el-form-item>
            <el-button type="primary" class="btnbg fr" @click="alertJournal"></el-button>
          </el-row>
        </el-form>
        <el-row class="table-wrap">
          <el-table :data="tableData" border style="text-again: center" class="all" @row-click="rowclickHandler" highlight-current-row>
            <el-table-column type='index' label=" " min-width="8" ></el-table-column>
            <el-table-column label="" min-width="200">
              <template scope="scope">
                <!-- {{scope.row.nType}} -->
                2017-07-23
              </template>
            </el-table-column>
            <el-table-column label="" min-width="120">
              <template scope="scope">
                <!-- {{scope.row.nType}} -->
                2017-07-23
              </template>
            </el-table-column>
            <el-table-column label="" min-width="90">
              <template scope="scope">
                <!-- {{scope.row.nType}} -->
                2017-07-23
              </template>
            </el-table-column>
            <el-table-column label="" min-width="90">
              <template scope="scope">
                <!-- {{scope.row.nType}} -->
                2017-07-23
              </template>
            </el-table-column>
            <el-table-column label="" min-width="80">
              <template scope="scope">
                <el-button type="text"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-row>
      </el-row>

    </jdy-alert>
    <!-- -  end-->


  </div>
</template>

<script>
  import jdyAlert from '@/components/Alert';
  import jdyLog from '@/components/Log';
  export default {
    components: {
      jdyAlert,
      jdyLog
    },
    name: 'information',
    data() {
      return {
        value6:'',
        alertMergeBillFlag:false,
        alertMergeBillSearchData:{},
        creditForm:{},
        msg: 'pivilege',
        commonTime: '',
        currentId: '',//id
        changeTypeValue: '',//
        searchValue: '',//
        isRecommendValue: '',//
        currentStatus: '',//
        alertNoticeFlag: false,
        alertJournalFlag: false,
        currentPage: 1, //,
        alertTitle: '',//
        alertContent: '',//
        pageSizeAll: 20,
        journalCurrentData: {}, //ww
        ruleForm: {
          dateRange: ''
        },
        searchInput: '',
        rules: {},
        type: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }],
        tableData: [],
        fullscreenLoading: false,
        tableDataTotal: 0,
        //
        pid: null,
        module: "News",
        journalTotal: 0,
        journalCurrentData: [], //


        //
        includeRevoked: 0,
        percentage:80
      }
    },
    mounted() {
      this.$http.post(api_prefix + '/News/list', {
        nType: '',
        searchKey: this.searchValue,
        currPage: this.currentPage,
        pageSize: 20
      }).then(response => {
        let data = response.data.body;
        this.tableData = data.list;
        this.tableDataTotal = data.total;
      }, response => {
        console.log('');
      });
    },
    computed: {
      journalTotal() { //
        return listData.journalData.length * 10
      }
    },
    methods: {
      getTableData(opt) {//table
        this.$http.post(api_prefix + '/News/list', {
          nType: this.changeTypeValue,
          searchKey: this.searchValue,
          pageSize: 20,
          currPage: this.currentPage
        }).then(response => {
          let data = response.data.body;
          this.tableData = data.list;
          this.tableDataTotal = data.total;
        }, response => {
          console.log('');
        });
      },


      //
      isRevoked(value) {
        this.includeRevoked = value;
        console.log(this.includeRevoked, 'this.includeRevoked')
      },
      //
      generateCreditBill(){
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => { });
      },
      //
      mergeCreditBill(){
        this.alertMergeBillFlag = true;
        this.$nextTick(function () {
          jdyFn.setAlertStyle('dialog-lv');
        })
      },
      closeAlert(){
        this.alertMergeBillFlag = false;
        $('.alertbgg').remove();
      },


      changeType(value) {//
        console.log(value)
        this.changeTypeValue = value;
        console.log(this.changeTypeValue)
      },
      searchKeyWords(value) {//
        console.log(value)
        this.searchValue = value;
      },
      handleIconClick(ev) {//
        this.getNoticeData();
      },
      getNoticeData() {//
        if (this.changeTypeValue != 3) {
          this.$http.post(api_prefix + '/News/list', {
            nType: this.changeTypeValue,
            searchKey: this.searchValue,
            currPage: this.currentPage,
            pageSize: 20
          }).then(response => {
            let data = response.data.body;
            this.tableData = data.list;
            this.tableDataTotal = data.total;
          }, response => {
            console.log('');
          });
        } else {
          this.$http.post(api_prefix + '/News/list', {
            searchKey: this.searchValue,
            currPage: this.currentPage,
            pageSize: 20
          }).then(response => {
            let data = response.data.body;
            this.tableData = data.list;
          }, response => {
            console.log('');
          });
        }
      },
      deleteInfor(id) {//
        this.currentId = id;
        console.log('ID', this.currentId)
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.post(api_prefix + '/News/updateStatus', {
            id: this.currentId,
            nStatus: 2,
          }).then(response => {
            this.fullscreenLoading = true;
            this.getTableData();
            setTimeout(() => {
              this.fullscreenLoading = false;
              this.$message({
                showClose: true,
                message: '',
                type: 'success'
              });
            }, 1000);
          }, response => {
            console.log('');
          });
        }).catch(() => {

        });
      },
      handleCurrentChange(val) {
        if (val) {
          var obj = { 'currentPage': val }
          this.getNewPage();
          this.currentPage = val;
        }
      },
      getNewPage() {
        this.$http.post(api_prefix + '/News/list', {
          nType: this.changeTypeValue,
          searchKey: this.searchValue,
          currPage: this.currentPage,
          pageSize: 20
        }).then(response => {
          let data = response.data.body;
          this.tableData = data.list;
          this.tableDataTotal = data.total;
        }, response => {
          console.log('');
        });
      },
      //---------- start-----
      //,
      alertJournal(alertJournalFlag) {
        if (!this.pid) { this.$alert(""); return }
        this.alertJournalFlag = !this.alertJournalFlag
        if (this.alertJournalFlag) {
          this.$nextTick(function () {
            jdyFn.setAlertStyle('alertJournal');
          });
        }
      },
      //
      getLogData(index) {
        this.$http.post(api_prefix + '/logs/selectLogs?pageIndex=' + index + '&module=' + this.module + '&pid=' + this.pid
        ).then(response => {
          let _self = this;
          this.journalTotal = response.data.body.total;
          this.journalCurrentData = []
          response.data.body.list.forEach(function (value) {
            _self.journalCurrentData.push(value);
          });
          // this.journalCurrentData=response.data.body.list
        }, response => {
          console.log('');
        });
      },
      //table
      rowclickHandler(row, event, column) {
        this.pid = row.id;
        if (!this.companyId) { this.companyId = row.companyId }
        this.getLogData(1)
      },
      //---------- end-----
    }
  }
</script>
<style scoped>
  .dialog-lv td{
    background-color: #fff;;
  }
</style>



