</template>

<script>
import jdyAlert from '@/components/Alert';
import jdyLog from '@/components/Log';
export default {
    components: {
        jdyAlert,
        jdyLog
    },
    name: 'information',
    data() {
        return {
            alertWriteOffFlag:false,
           creditForm:{},
            msg: 'pivilege',
            commonTime: '',
            currentId: '',//id
            changeTypeValue: '',//
            searchValue: '',//
            isRecommendValue: '',//
            currentStatus: '',//
            alertNoticeFlag: false,
            alertJournalFlag: false,
            currentPage: 1, //,
            alertTitle: '',//
            alertContent: '',//
            pageSizeAll: 20,
            journalCurrentData: {}, //ww
            ruleForm: {
                dateRange: ''
            },
            searchInput: '',
            rules: {},
            type: [{
                value: '1',
                label: ''
            }, {
                value: '2',
                label: ''
            }, {
                value: '3',
                label: ''
            }],
            tableData: [],
            fullscreenLoading: false,
            tableDataTotal: 0,
            //
            pid: null,
            module: "News",
            journalTotal: 0,
            journalCurrentData: [], //


            //
            includeRevoked: 0,
            percentage:80
        }
    },
    mounted() {
        this.$http.post(api_prefix + '/News/list', {
            nType: '',
            searchKey: this.searchValue,
            currPage: this.currentPage,
            pageSize: 20
        }).then(response => {
            let data = response.data.body;
            this.tableData = data.list;
            this.tableDataTotal = data.total;
        }, response => {
            console.log('');
        });
    },
    computed: {
        journalTotal() { //
            return listData.journalData.length * 10
        }
    },
    filters: {
        changeType: function (value) {
            if (value == '1') {
                return value = ''
            } else {
                return value = ''
            }
        },
        changeCreate: function (value) {
            console.log(1)
            var unixTimestamp = new Date(value);
            var oYear = unixTimestamp.getFullYear();
            var oMonth = unixTimestamp.getMonth() + 1;
            var oDay = unixTimestamp.getDate();
            var oHour = unixTimestamp.getHours();
            var oMin = unixTimestamp.getMinutes();
            var oSen = unixTimestamp.getSeconds();
            var oTime = oYear + '-' + getzf(oMonth) + '-' + getzf(oDay) + ' ' + getzf(oHour) + ':' + getzf(oMin) + ':' + getzf(oSen);//
            //0
            function getzf(num) {
                if (parseInt(num) < 10) {
                    num = '0' + num;
                }
                return num;
            }
            return value = oTime;
        },
        changeUpdateTime: function (value) {
            if (value == undefined) {
                return value = ''
            } else {
                var unixTimestamp = new Date(value);
                var Year = unixTimestamp.getFullYear();
                var Month = unixTimestamp.getMonth() + 1;
                var Day = unixTimestamp.getDate();
                var Hour = unixTimestamp.getHours();
                var Min = unixTimestamp.getMinutes();
                var Sen = unixTimestamp.getSeconds();
                var Time = Year + '-' + getzf(Month) + '-' + getzf(Day) + ' ' + getzf(Hour) + ':' + getzf(Min) + ':' + getzf(Sen);//
                //0
                function getzf(num) {
                    if (parseInt(num) < 10) {
                        num = '0' + num;
                    }
                    return num;
                }
                return value = Time;
            }
        }
    },
    methods: {
        getTableData(opt) {//table
            this.$http.post(api_prefix + '/News/list', {
                nType: this.changeTypeValue,
                searchKey: this.searchValue,
                pageSize: 20,
                currPage: this.currentPage
            }).then(response => {
                let data = response.data.body;
                this.tableData = data.list;
                this.tableDataTotal = data.total;
            }, response => {
                console.log('');
            });
        },


        //
        isRevoked(value) {
            this.includeRevoked = value;
            console.log(this.includeRevoked, 'this.includeRevoked')
        },
        //
        printBill(){
          this.$router.push({ name: "creditBillPrint",query:{type: 1}})
        },
        //
        removeBill(){
          this.$router.push({ name: "creditBillRemove",query:{type: 1}})
        },
        //
        writeOff(){
          this.alertWriteOffFlag = true;
          this.$nextTick(function(){
            jdyFn.setAlertStyle('dialog-lv');
          });
        },
        closeAlert(){
          this.alertWriteOffFlag = false;
          $('.alertbgg').remove();
        },
        saveWriteOffBtn(){
          this.closeAlert();
        },
        //
        revokeBill(){
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$message({
              type: 'success',
              message: '!'
            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        },
        changeType(value) {//
            console.log(value)
            this.changeTypeValue = value;
            console.log(this.changeTypeValue)
        },
        searchKeyWords(value) {//
            console.log(value)
            this.searchValue = value;
        },
        handleIconClick(ev) {//
            this.getNoticeData();
        },
        getNoticeData() {//
            if (this.changeTypeValue != 3) {
                this.$http.post(api_prefix + '/News/list', {
                    nType: this.changeTypeValue,
                    searchKey: this.searchValue,
                    currPage: this.currentPage,
                    pageSize: 20
                }).then(response => {
                    let data = response.data.body;
                    this.tableData = data.list;
                    this.tableDataTotal = data.total;
                }, response => {
                    console.log('');
                });
            } else {
                this.$http.post(api_prefix + '/News/list', {
                    searchKey: this.searchValue,
                    currPage: this.currentPage,
                    pageSize: 20
                }).then(response => {
                    let data = response.data.body;
                    this.tableData = data.list;
                }, response => {
                    console.log('');
                });
            }
        },
        deleteInfor(id) {//
            this.currentId = id;
            console.log('ID', this.currentId)
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$http.post(api_prefix + '/News/updateStatus', {
                    id: this.currentId,
                    nStatus: 2,
                }).then(response => {
                    this.fullscreenLoading = true;
                    this.getTableData();
                    setTimeout(() => {
                        this.fullscreenLoading = false;
                        this.$message({
                            showClose: true,
                            message: '',
                            type: 'success'
                        });
                    }, 1000);
                }, response => {
                    console.log('');
                });
            }).catch(() => {

            });
        },
        handleCurrentChange(val) {
            if (val) {
                var obj = { 'currentPage': val }
                this.getNewPage();
                this.currentPage = val;
            }
        },
        getNewPage() {
            this.$http.post(api_prefix + '/News/list', {
                nType: this.changeTypeValue,
                searchKey: this.searchValue,
                currPage: this.currentPage,
                pageSize: 20
            }).then(response => {
                let data = response.data.body;
                this.tableData = data.list;
                this.tableDataTotal = data.total;
            }, response => {
                console.log('');
            });
        },
        //---------- start-----
        //,
        alertJournal(alertJournalFlag) {
            if (!this.pid) { this.$alert(""); return }
            this.alertJournalFlag = !this.alertJournalFlag
            if (this.alertJournalFlag) {
                this.$nextTick(function () {
                    jdyFn.setAlertStyle('alertJournal');
                });
            }
        },
        //
        getLogData(index) {
            this.$http.post(api_prefix + '/logs/selectLogs?pageIndex=' + index + '&module=' + this.module + '&pid=' + this.pid
            ).then(response => {
                let _self = this;
                this.journalTotal = response.data.body.total;
                this.journalCurrentData = []
                response.data.body.list.forEach(function (value) {
                    _self.journalCurrentData.push(value);
                });
                // this.journalCurrentData=response.data.body.list
            }, response => {
                console.log('');
            });
        },
        //table
        rowclickHandler(row, event, column) {
            this.pid = row.id;
            if (!this.companyId) { this.companyId = row.companyId }
            this.getLogData(1)
        },
        //---------- end-----
    }
}
</script>
<style scoped>
  .dialog-credit{
    width: 660px;
  }
</style>



