<template>
    <div class="jl-information">
      <div class="jdy-content fleft jl-creditBill">
        <div class="jdy-content-inner">
          <el-row class="search-row">
            <el-form :model="ruleForm" :inline="true" :rules="rules" ref="ruleForm" class="mt10">
              <el-row>
                <el-form-item label="" prop="type">
                  <el-select v-model="ruleForm.type" placeholder="" class="select-m">
                    <!--<el-option v-for="item in companiesOptions" :label="item.cName" :value="item.id" :key="item.id">
                    </el-option>-->
                    <el-option  label="gong1" value="gong1" key="gong1">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="" prop="type">
                  <el-select v-model="ruleForm.type" placeholder="" class="select-m">
                    <!--<el-option v-for="item in companiesOptions" :label="item.cName" :value="item.id" :key="item.id">
                    </el-option>-->
                    <el-option  label="gong1" value="gong1" key="gong1">
                    </el-option>
                  </el-select>
                </el-form-item>

                <el-form-item label="" prop="quickSearch">
                  <el-input placeholder="//" @change="searchKeyWords" v-model="searchValue">
                  </el-input>
                </el-form-item>
                <el-button type="primary" class="btnbg" @click="alertJournal"></el-button>
                <div class="btn-wrap fright">
                  <el-button @click="updatePage" v-loading.fullscreen.lock="fullscreenLoading"></el-button>
                  <el-button @click="alertJournal"></el-button>
                  <el-button @click="addCreditFn"></el-button>
                </div>
              </el-row>
            </el-form>
          </el-row>
          <div class="jdy-table pt10">
            <el-table :data="tableData" style="width: 100%"  border>
              <el-table-column label="" type="index"  width="60"></el-table-column>
              <el-table-column label="" prop="ticketType" min-width="220"></el-table-column>
              <el-table-column label="" prop="ticketPrice"  min-width="220"></el-table-column>
              <el-table-column label="" prop="ticketNum" min-width="220"></el-table-column>
              <el-table-column label="" prop="bedNum" min-width="90"></el-table-column>
              <el-table-column label="" prop="stock" min-width="100"></el-table-column>
              <el-table-column label=" " prop="ticketPrice" min-width="100"></el-table-column>
              <el-table-column label=" " prop="ticketPrice" min-width="90"></el-table-column>
              <el-table-column label=" " prop="ticketPrice" min-width="90"></el-table-column>
              <el-table-column label="" prop="ticketPrice"  min-width="70">
                <template scope="scope">
                  <el-button @click="editCredit(scope.row.id)" class="td-btn"></el-button>
                </template>
              </el-table-column>
            </el-table>
            <!--    begin-->
            <div class="clearfix">
              <el-pagination class="fright mt20" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pageSizeAll" layout="prev, pager, next, jumper" :total="tableDataTotal">
              </el-pagination>
            </div>
            <!--    end-->
          </div>

        </div>
      </div>
      <!--jdy-content end-->

      <!--   begin-->
      <jdy-alert title="" @closeAlert="closeAlert" v-if="alertCreditFlag" class="dialog-lv dialog-credit">
        <el-row class="scroll-cont form-wrap p20 pb0">
          <div class="sec-wrap">
            <div class="sec-title"><span> </span></div>
            <el-form label-width="120px" :model="creditForm">
              <el-form-item label="">
                <el-input v-model="creditForm.name" :disabled="true"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="creditForm.region" :disabled="true"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-checkbox-group v-model="creditForm.type">
                  <el-checkbox label="" name="type"></el-checkbox>
                  <el-checkbox label="" name="type"></el-checkbox>
                  <el-checkbox label="" name="type"></el-checkbox>
                  <el-checkbox label="" name="type"></el-checkbox>
                  <el-checkbox label="" name="type"></el-checkbox>
                  <el-checkbox label="" name="type"></el-checkbox>
                </el-checkbox-group>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model="creditForm.region">
                  <el-option label="" value="shanghai"></el-option>
                  <el-option label="" value="beijing"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model="creditForm.region">
                  <el-option label="" value="shanghai"></el-option>
                  <el-option label="" value="beijing"></el-option>
                </el-select>
              </el-form-item>
            </el-form>
          </div>
          <div class="sec-wrap">
            <div class="sec-title"> <span></span> </div>
            <div class="tips">
              <p>19999-99-99</p>
              <p>2
                </p>
              <p>3</p>
              <p>4 </p>
              <p>5</p>
            </div>
          </div>
        </el-row>
        <el-row class="btn-wrap">
          <el-button type="primary"  @click="saveSeatChange"></el-button>
          <el-button @click="closeAlert"></el-button>
        </el-row>
      </jdy-alert>
      <!--   end-->

      <!--   begin-->
      <jdy-alert title="" v-if="alertJournalFlag" @closeAlert="alertJournal" class="alertJournal">
          <jdy-log :alertJournalFlag="alertJournalFlag" :journalCurrentData="journalCurrentData" :journalTotal="journalTotal" :pid="pid" :module="module" @listenToChildEvent="alertJournal"></jdy-log>
      </jdy-alert>
      <!--   begin-->
    </div>
</template>

<script>
import jdyAlert from '@/components/Alert';
import jdyLog from '@/components/Log';
export default {
    components: {
        jdyAlert,
        jdyLog
    },
    name: 'information',
    data() {
        return {
          alertCreditFlag:false,
          fullscreenLoading: false,
          creditForm:{},
          companiesOptions:[],
            msg: 'pivilege',
            commonTime: '',
            currentId: '',//id
            changeTypeValue: '',//
            searchValue: '',//
            isRecommendValue: '',//
            currentStatus: '',//
            alertNoticeFlag: false,
            alertJournalFlag: false,
            currentPage: 1, //,

            pageSizeAll: 20,
            journalCurrentData: {}, //ww
            ruleForm: {
                dateRange: ''
            },
            searchInput: '',
            rules: {},
            type: [{
                value: '1',
                label: ''
            }, {
                value: '2',
                label: ''
            }, {
                value: '3',
                label: ''
            }],
            tableData: [],
            fullscreenLoading: false,
            tableDataTotal: 0,
            //
            pid: null,
            module: "News",
            journalTotal: 0,
            journalCurrentData: [], //


            //
            includeRevoked: 0
        }
    },
    mounted() {
        this.$http.post(api_prefix + '/News/list', {
            nType: '',
            searchKey: this.searchValue,
            currPage: this.currentPage,
            pageSize: 20
        }).then(response => {
            let data = response.data.body;
            this.tableData = data.list;
            this.tableDataTotal = data.total;
        }, response => {
            console.log('');
        });
    },
    computed: {
        journalTotal() { //
            return listData.journalData.length * 10
        }
    },
    methods: {
      closeAlert(){
        this.alertCreditFlag = false;
        $('.alertbgg').remove();
      },
      handleIconClick(){

      },
      //
      updatePage() {
        this.fullscreenLoading = true;
        setTimeout(() => {
          this.fullscreenLoading = false;
          this.$message({
            showClose: true,
            message: '',
            type: 'success'
          });
          this.getTableData('all');
        }, 1000);
      },
      //
      addCreditFn(){
        this.$router.push({name: 'creditAdd'});
      },
      //
      editCredit(listId){
        this.alertCreditFlag = true;
      },
      handleCurrentChange(val) {
          if (val) {
              var obj = { 'currentPage': val }
              this.getNewPage();
              this.currentPage = val;
          }
      },
      getNewPage() {
          this.$http.post(api_prefix + '/News/list', {
              nType: this.changeTypeValue,
              searchKey: this.searchValue,
              currPage: this.currentPage,
              pageSize: 20
          }).then(response => {
              let data = response.data.body;
              this.tableData = data.list;
              this.tableDataTotal = data.total;
          }, response => {
              console.log('');
          });
      },
      //---------- start-----
      //,
      alertJournal(alertJournalFlag) {
          if (!this.pid) { this.$alert(""); return }
          this.alertJournalFlag = !this.alertJournalFlag
          if (this.alertJournalFlag) {
              this.$nextTick(function () {
                  jdyFn.setAlertStyle('alertJournal');
              });
          }
      },
      //
      getLogData(index) {
          this.$http.post(api_prefix + '/logs/selectLogs?pageIndex=' + index + '&module=' + this.module + '&pid=' + this.pid
          ).then(response => {
              let _self = this;
              this.journalTotal = response.data.body.total;
              this.journalCurrentData = []
              response.data.body.list.forEach(function (value) {
                  _self.journalCurrentData.push(value);
              });
              // this.journalCurrentData=response.data.body.list
          }, response => {
              console.log('');
          });
      },
      //table
      rowclickHandler(row, event, column) {
          this.pid = row.id;
          if (!this.companyId) { this.companyId = row.companyId }
          this.getLogData(1)
      },
      //---------- end-----
    }
}
</script>
<style>


</style>



