<template>
    <div class="jl-information">
        <div class="jdy-content jdy-transfer fleft jl-creditBill">
            <div class="jdy-content-inner">
                <el-form :model="ruleForm" :inline="true" :rules="rules" ref="ruleForm" class="mt10">
                    <el-row class="search-row" style="width: 70%;">
                        <el-form-item label="" prop="type">
                          <el-select v-model="ruleForm.type" placeholder="" @change="changeType" class="select-m">
                            <el-option v-for="item in type" :key="item.value" :label="item.label" :value="item.value">
                            </el-option>
                          </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="type">
                          <el-select v-model="ruleForm.type" placeholder="" @change="changeType" class="select-m">
                            <el-option v-for="item in type" :key="item.value" :label="item.label" :value="item.value">
                            </el-option>
                          </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="type">
                          <el-select v-model="ruleForm.type" placeholder="" @change="changeType" class="select-m">
                            <el-option v-for="item in type" :key="item.value" :label="item.label" :value="item.value">
                            </el-option>
                          </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="type">
                          <el-select v-model="ruleForm.type" placeholder="" @change="changeType" class="select-m">
                            <el-option v-for="item in type" :key="item.value" :label="item.label" :value="item.value">
                            </el-option>
                          </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-date-picker v-model="ruleForm.dateRange" :picker-options="pickerOptions" type="daterange" @change="datetimerange" placeholder="" class="input-date-l">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item label="" prop="quickSearch" class="creditBillSearch">
                            <el-input placeholder="////" @change="searchKeyWords" v-model="searchValue">
                            </el-input>
                        </el-form-item>
                        <el-button type="primary" class="ml10 btnbg" @click="alertJournal"></el-button>
                       <el-button type="default" @click="generateCreditBill"></el-button>
                    </el-row>
                </el-form>
                <div class="jdy-table pt10">
                <el-table :data="tableData" border style="text-again: center" class="all" @row-click="rowclickHandler" highlight-current-row>
                    <el-table-column type='index' label="" width="65"></el-table-column>
                    <el-table-column label="" min-width="250">
                        <template scope="scope">
                            <!-- {{scope.row.nType}} -->
                            
                        </template>
                    </el-table-column>
                    <el-table-column label="" min-width="250"></el-table-column>
                    <el-table-column label="" min-width="100">22</el-table-column>
                    <el-table-column label="" min-width="100">2448.00</el-table-column>
                    <el-table-column label="" min-width="190">
                      <template scope="scope">
                        <!-- {{scope.row.nType}} -->
                        2448.00
                      </template>
                    </el-table-column>
                    <el-table-column label="(-)" min-width="150">
                        <template scope="scope">
                            <!-- {{scope.row.nType}} -->
                          2017/7/1-2017/8/9
                        </template>
                    </el-table-column>
                  <el-table-column label="(-)" min-width="150">
                    <template scope="scope">
                      <!-- {{scope.row.nType}} -->
                      2017/7/1-2017/8/9
                    </template>
                  </el-table-column>
                </el-table>
                <!--    begin-->
                <div class="clearfix">
                    <el-pagination class="fright mt20" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pageSizeAll" layout="prev, pager, next, jumper" :total="tableDataTotal">
                    </el-pagination>
                </div>
                <!--    end-->
            </div>
            </div>
        </div>
        <!--jdy-content end-->

      <!-- -  begin-->
      <jdy-alert title="" @closeAlert="closeAlert" v-if="alertWriteOffFlag" class="dialog-lv dialog-credit">
        <el-row class="scroll-cont form-wrap p20 pb0">
          <el-form label-width="120px" :model="creditForm">
            <el-form-item label="">
              <el-input v-model="creditForm.name"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="creditForm.region"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-date-picker type="date" placeholder="" v-model="creditForm.date1"></el-date-picker>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="creditForm.name"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="creditForm.region"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="creditForm.region">
                <el-option label="" value="shanghai"></el-option>
                <el-option label="" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="creditForm.region">
                <el-option label="" value="shanghai"></el-option>
                <el-option label="" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input type="textarea" v-model="creditForm.desc"></el-input>
            </el-form-item>
            <el-form-item label="">
              <p><span class="orange-font"></span>[60] ()</p>
            </el-form-item>
          </el-form>
        </el-row>
        <el-row class="btn-wrap">
          <el-button type="primary"  @click="saveWriteOffBtn"></el-button>
          <el-button @click="closeAlert"></el-button>
        </el-row>
      </jdy-alert>
      <!-- -  end-->

        <!--   begin-->
        <jdy-alert title="" v-if="alertJournalFlag" @closeAlert="alertJournal" class="alertJournal">
            <jdy-log :alertJournalFlag="alertJournalFlag" :journalCurrentData="journalCurrentData" :journalTotal="journalTotal" :pid="pid" :module="module" @listenToChildEvent="alertJournal"></jdy-log>
        </jdy-alert>
        <!--   begin-->

    </div>
</template>

<script>
import jdyAlert from '@/components/Alert';
import jdyLog from '@/components/Log';
export default {
    components: {
        jdyAlert,
        jdyLog
    },
    name: 'information',
    data() {
        return {
            alertWriteOffFlag:false,
           creditForm:{},
            msg: 'pivilege',
            commonTime: '',
            currentId: '',//id
            changeTypeValue: '',//
            searchValue: '',//
            isRecommendValue: '',//
            currentStatus: '',//
            alertNoticeFlag: false,
            alertJournalFlag: false,
            currentPage: 1, //,
            alertTitle: '',//
            alertContent: '',//
            pageSizeAll: 20,
            journalCurrentData: {}, //ww
            ruleForm: {
                dateRange: ''
            },
            searchInput: '',
            rules: {},
            type: [{
                value: '1',
                label: ''
            }, {
                value: '2',
                label: ''
            }, {
                value: '3',
                label: ''
            }],
            tableData: [],
            fullscreenLoading: false,
            tableDataTotal: 0,
            //
            pid: null,
            module: "News",
            journalTotal: 0,
            journalCurrentData: [], //


            //
            includeRevoked: 0,
            percentage:80
        }
    },
    mounted() {
        this.$http.post(api_prefix + '/News/list', {
            nType: '',
            searchKey: this.searchValue,
            currPage: this.currentPage,
            pageSize: 20
        }).then(response => {
            let data = response.data.body;
            this.tableData = data.list;
            this.tableDataTotal = data.total;
        }, response => {
            console.log('');
        });
    },
    computed: {
        journalTotal() { //
            return listData.journalData.length * 10
        }
    },
    methods: {
        getTableData(opt) {//table
            this.$http.post(api_prefix + '/News/list', {
                nType: this.changeTypeValue,
                searchKey: this.searchValue,
                pageSize: 20,
                currPage: this.currentPage
            }).then(response => {
                let data = response.data.body;
                this.tableData = data.list;
                this.tableDataTotal = data.total;
            }, response => {
                console.log('');
            });
        },


        //
        isRevoked(value) {
            this.includeRevoked = value;
            console.log(this.includeRevoked, 'this.includeRevoked')
        },
        //
        generateCreditBill(){
          this.$router.push({ name: "generateCreditBillByManual",query:{type: 1}})
        },
        //
        writeOff(){
          this.alertWriteOffFlag = true;
          this.$nextTick(function(){
            jdyFn.setAlertStyle('dialog-lv');
          });
        },
        closeAlert(){
          this.alertWriteOffFlag = false;
          $('.alertbgg').remove();
        },
        saveWriteOffBtn(){
          this.closeAlert();
        },
        //
        revokeBill(){
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$message({
              type: 'success',
              message: '!'
            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        },
        changeType(value) {//
            console.log(value)
            this.changeTypeValue = value;
            console.log(this.changeTypeValue)
        },
        searchKeyWords(value) {//
            console.log(value)
            this.searchValue = value;
        },
        handleIconClick(ev) {//
            this.getNoticeData();
        },
        getNoticeData() {//
            if (this.changeTypeValue != 3) {
                this.$http.post(api_prefix + '/News/list', {
                    nType: this.changeTypeValue,
                    searchKey: this.searchValue,
                    currPage: this.currentPage,
                    pageSize: 20
                }).then(response => {
                    let data = response.data.body;
                    this.tableData = data.list;
                    this.tableDataTotal = data.total;
                }, response => {
                    console.log('');
                });
            } else {
                this.$http.post(api_prefix + '/News/list', {
                    searchKey: this.searchValue,
                    currPage: this.currentPage,
                    pageSize: 20
                }).then(response => {
                    let data = response.data.body;
                    this.tableData = data.list;
                }, response => {
                    console.log('');
                });
            }
        },
        deleteInfor(id) {//
            this.currentId = id;
            console.log('ID', this.currentId)
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$http.post(api_prefix + '/News/updateStatus', {
                    id: this.currentId,
                    nStatus: 2,
                }).then(response => {
                    this.fullscreenLoading = true;
                    this.getTableData();
                    setTimeout(() => {
                        this.fullscreenLoading = false;
                        this.$message({
                            showClose: true,
                            message: '',
                            type: 'success'
                        });
                    }, 1000);
                }, response => {
                    console.log('');
                });
            }).catch(() => {

            });
        },
        handleCurrentChange(val) {
            if (val) {
                var obj = { 'currentPage': val }
                this.getNewPage();
                this.currentPage = val;
            }
        },
        getNewPage() {
            this.$http.post(api_prefix + '/News/list', {
                nType: this.changeTypeValue,
                searchKey: this.searchValue,
                currPage: this.currentPage,
                pageSize: 20
            }).then(response => {
                let data = response.data.body;
                this.tableData = data.list;
                this.tableDataTotal = data.total;
            }, response => {
                console.log('');
            });
        },
        //---------- start-----
        //,
        alertJournal(alertJournalFlag) {
            if (!this.pid) { this.$alert(""); return }
            this.alertJournalFlag = !this.alertJournalFlag
            if (this.alertJournalFlag) {
                this.$nextTick(function () {
                    jdyFn.setAlertStyle('alertJournal');
                });
            }
        },
        //
        getLogData(index) {
            this.$http.post(api_prefix + '/logs/selectLogs?pageIndex=' + index + '&module=' + this.module + '&pid=' + this.pid
            ).then(response => {
                let _self = this;
                this.journalTotal = response.data.body.total;
                this.journalCurrentData = []
                response.data.body.list.forEach(function (value) {
                    _self.journalCurrentData.push(value);
                });
                // this.journalCurrentData=response.data.body.list
            }, response => {
                console.log('');
            });
        },
        //table
        rowclickHandler(row, event, column) {
            this.pid = row.id;
            if (!this.companyId) { this.companyId = row.companyId }
            this.getLogData(1)
        },
        //---------- end-----
    }
}
</script>




